import React from 'react';

const listForItter = [];

for (let i = 0; i < 17; i++) {
   listForItter.push(i);
}

function ListChildren(props) {

   return (
      <div className="list-children-body">
         {listForItter.map(elem =>
            <div key={elem} className="adult-count" onClick={props.change.bind(this, elem + 1)}>Ребёнок {elem + 1} год</div>
         )}
      </div>
   );
}

export default ListChildren;