import React from 'react';
import './fonts/fonts.css';
import './AddGuests.css';
import CreateHouse from './CreateHouse';
import ButtonHouse from './ButtonHouse';
import { useState } from 'react';

/*
   Отсюда брать количество номеров(listNumbers.length)
*/


localStorage.clear();
console.log(localStorage);

const listNumbers = [
   {
      id: 0,
      adults: 0,
      children: []
   }
];

localStorage.houses = JSON.stringify(listNumbers);
// console.log(JSON.parse(localStorage.houses));

const houseImgLink = "https://img2.freepng.ru/20180405/vke/kisspng-house-computer-icons-building-clip-art-home-5ac6a306f14eb6.1409947515229673029884.jpg";
const addHouseText = "Добавить номер";
const deleteHouseText = "Удалить номер";

function AddGuests() {
   const [listNumbersState, setListNumbersState] = useState(listNumbers);

   function addHouse() {
      // console.log("working :)");
      const updateList = Array.from(listNumbersState);
      if (listNumbersState.length === 0) {
         let buffObject = {
            id: 0,
            adults: 0,
            children: []
         };
         updateList.push(buffObject);
      }
      else {
         let idNew = listNumbersState[listNumbersState.length - 1].id + 1;
         let buffObject = {
            id: idNew,
            adults: 0,
            children: []
         };
         updateList.push(buffObject);
      }
      localStorage.houses = JSON.stringify(updateList);
      // console.log(JSON.parse(localStorage.houses));
      setListNumbersState(updateList);
   }

   function deleteHouse() {
      // console.log("working too :)");
      const updateList = Array.from(listNumbersState);
      updateList.pop();
      localStorage.houses = JSON.stringify(updateList);
      // console.log(JSON.parse(localStorage.houses));
      setListNumbersState(updateList);
   }

   return (
      <>
         <div className='add-guests-main'>
            {listNumbersState.map(elem =>
               <CreateHouse key={elem.id} data={elem} number={elem.id}></CreateHouse>
            )}
            <div className='container-buttons-add-delete'>
               <ButtonHouse url={houseImgLink} text={addHouseText} onClick={addHouse}></ButtonHouse>
               <ButtonHouse url={houseImgLink} text={deleteHouseText} onClick={deleteHouse}></ButtonHouse>
            </div>
         </div>
      </>
   );
}

export default AddGuests;