import React from 'react';

const listForItter = [];

for (let i = 0; i < 40; i++) {
   listForItter.push(i);
}

function ListAdults(props) {
   return (
      <div className="list-adults-body">
         {listForItter.map(elem =>
            <div key={elem} className="adult-count" onClick={props.onUpdate.bind(this, elem)}>{elem + 1} взрослый</div>
         )}
      </div>
   );
}

export default ListAdults;