import React from 'react';

function ButtonHouseCh(props) {
   return (
      <div className="button-house transition" onClick={props.onClick.bind(this, props.addCh)}>
         <img className="img-button" src={props.url} alt="button" />
         <div className="description-button">{props.text}</div>
      </div>
   );
}

export default ButtonHouseCh;