import ButtonHouse from "./ButtonHouse";
import ButtonHouseCh from "./ButtonHouseCh";
import ButtonChildren from "./ButtonChildren";
import ListAdults from "./ListAdults";
import React from "react";
import { useState } from 'react';

/*
   "house" + String(props.number) + "ad"; -> количество взрослых БРАТЬ ОТСЮДА
*/



const imgAdult = "http://cdn.onlinewebfonts.com/svg/img_209846.png";
const imgChild = "https://flyclipart.com/thumb2/babys-room-icon-543113.png";

let listChildren = [
];

const addChildrenText = "Добавить детей";

function CreateHouse(props) {

   let getLocalStorageKeyChildrenList = String(props.number); // номер дома
   // console.log("Cписок детей -> ", getLocalStorageKeyChildrenList);
   // console.log(JSON.parse(localStorage.getItem(getLocalStorageKeyChildrenList)));

   let [buttonClick, setButtonClick] = useState(1);
   let [isShowAdult, setIsShowAdult] = useState(false);
   let [countAdults, setCountAdults] = useState(1);

   let getLocalStorageKeyAdult = "house" + String(props.number) + "ad";
   // console.log("Количество взрослых -> ", getLocalStorageKeyAdult);
   localStorage.setItem(getLocalStorageKeyAdult, countAdults);
   // console.log(localStorage.getItem(getLocalStorageKeyAdult));


   let [listChildrenState, setListChildren] = useState(listChildren);

   function showAdultsCount() {
      if (isShowAdult) {
         setIsShowAdult(false);
      }
      else {
         setIsShowAdult(true);
      }
   }

   function changeAdultsCount(count) {
      // console.log(count + 1);
      // console.log("ok");
      setCountAdults(count + 1);
      localStorage.setItem(getLocalStorageKeyAdult, count + 1);
      console.log(localStorage);
      setIsShowAdult(false);
   }


   function addChildren(str) {
      setButtonClick(buttonClick + 1);
      // console.log("here -> ", buttonClick);
      let updateList = [];
      let bufObject;
      updateList = Array.from(listChildrenState);
      if (listChildrenState.length === 0) {
         bufObject = {
            id: 0,
            text: "Ребёнок 1 год"
         }
      }
      else {
         bufObject = {
            id: buttonClick,
            text: "Ребёнок 1 год"
         }
      }
      updateList.push(bufObject);
      // localStorage.setItem(str, "Ребёнок 1 год");
      // console.log(getLocalStorageKeyChildrenList + "ch" + String(str));
      localStorage.setItem(getLocalStorageKeyChildrenList + "ch" + String(str), "Ребёнок 1 год");
      console.log(localStorage);
      setListChildren(updateList);
   }

   function onChildDelete(number) {
      // console.log(number);
      let updateList = [];
      updateList = Array.from(listChildrenState);
      // console.log("before -> ", updateList);
      // console.log("number -> ", number);
      updateList.splice(number, 1);
      // console.log("after -> ", updateList);
      localStorage.removeItem(getLocalStorageKeyChildrenList + "ch" + String(number));
      console.log(localStorage);
      setListChildren(updateList);
   }

   function findObjectArray(idElem) {
      for (let i = 0; i < listChildrenState.length; i++) {
         if (listChildrenState[i].id === idElem) {
            return i;
         }
      }
   }

   const textAdult = `${countAdults} взрослый`;

   return (
      <div className="container-house">
         <div className="house-number">Номер №{props.number + 1}</div>
         <div className="container-buttons-add-delete">
            <div className="container-buttons-add-addult-child">
               <ButtonHouse url={imgAdult} text={textAdult} onClick={showAdultsCount}></ButtonHouse>
               {isShowAdult && <ListAdults onUpdate={changeAdultsCount}></ListAdults>}
            </div>
            <div className="container-buttons-add-addult-child">
               {listChildrenState.map(elem =>
                  <React.Fragment key={elem.id + 1000}>
                     <ButtonChildren key={elem.id} url={imgChild} delete={onChildDelete} number={findObjectArray(elem.id)} local={getLocalStorageKeyChildrenList + "ch" + String(findObjectArray(elem.id))}></ButtonChildren>
                  </React.Fragment>
               )}
               <ButtonHouseCh url={imgChild} text={addChildrenText} onClick={addChildren} addCh={listChildrenState.length}></ButtonHouseCh>
            </div>
         </div>
      </div>
   );
}

export default CreateHouse;