import ListChildren from "./ListChildren";
import { useState } from 'react';
import React from 'react';

/*
   Возраст детей брать отсюда {номер дома}ch{номер ребёнка} -> по этим данным количество детей в каждом номере
*/

function ButtonChildren(props) {

   let strLocal = props.local;

   let [text, setText] = useState("Ребёнок 1 год");
   let [isShow, setIsShow] = useState(false);

   function showList() {
      if (isShow) {
         setIsShow(false);
      }
      else {
         setIsShow(true);
      }
   }

   function changeAgeChild(count) {
      if (isShow) {
         setIsShow(false);
      }
      else {
         setIsShow(true);
      }
      setText(`Ребёнок ${count} год`);
      localStorage.setItem(strLocal, `Ребёнок ${count} год`);
      // console.log("here after change -> ", localStorage.getItem(strLocal));
      console.log("age -> ", localStorage.getItem(strLocal));
      console.log(localStorage);
   }

   return (
      <>
         <div className="button-house-child transition">
            <img className="img-button" src={props.url} alt="button" onClick={showList} />
            <div className="description-button-child" onClick={showList}>{text}</div>
            <div className="delete-child" onClick={props.delete.bind(this, props.number)}>X</div>
         </div>
         {isShow && <ListChildren change={changeAgeChild}></ListChildren>}
      </>
   );
}

export default ButtonChildren;